import * as types from './mutation-types'
import { playMode } from '@/common/js/config'
import { randomSort } from '@/common/js/util'
import { searchHistory, deleteSearch, clearSearch,playHistory } from '@/common/js/cache'

function findIndex(list, song) {
	return list.findIndex((item) => {
		return item.id === song.id
	})

}
export const selectPlay = function({
	commit,
	state
}, {
	list,
	index
}) {
	commit(types.SET_SEQUENCELIST, list)
	if(state.mode === playMode.random) {
		let randomList = randomSort(list)
		commit(types.SET_PLAYLIST, randomList)
		commit(types.SET_CURRENT_INDEX, findIndex(randomList, list[index]))

	} else {
		commit(types.SET_PLAYLIST, list)
		commit(types.SET_CURRENT_INDEX, index)
	}

	commit(types.SET_PLAYING, true)
	commit(types.SET_FULLSCREEN, true)

}

export const randomPlay = function({
	commit,
	state
}, {
	list,
	index
}) {
	commit(types.SET_PLAY_MODE, playMode.random)
	commit(types.SET_SEQUENCELIST, list)
	let randomList = randomSort(list)

	commit(types.SET_PLAYLIST, randomList)
	commit(types.SET_PLAYING, true)
	commit(types.SET_FULLSCREEN, true)
	commit(types.SET_CURRENT_INDEX, 0)

}

export const insertSong = function({
	commit,
	state
}, song) {
	let playList = state.playList.slice()
	let sequenceList = state.sequenceList.slice()
	let currentIndex = state.currentIndex
	// 记录当前歌曲
	let currentSong = playList[currentIndex]
	// 查找当前列表中是否有待插入的歌曲并返回其索引
	let fpIndex = findIndex(playList, song)
	// 因为是插入歌曲，所以索引+1
	currentIndex++
	// 插入这首歌到当前索引位置
	playList.splice(currentIndex, 0, song)
	// 如果已经包含了这首歌
	if(fpIndex > -1) {
		// 如果当前插入的序号大于列表中的序号
		if(currentIndex > fpIndex) {
			playList.splice(fpIndex, 1)
			currentIndex--
		} else {
			playList.splice(fpIndex + 1, 1)
		}
	}
	let currentSIndex = findIndex(sequenceList, currentSong) + 1
	let fsIndex = findIndex(sequenceList, song)
	sequenceList.splice(currentSIndex, 0, song)
	if(fsIndex > -1) {
		if(currentSIndex > fsIndex) {
			sequenceList.splice(fsIndex, 1)
		} else {
			sequenceList.splice(fsIndex + 1, 1)
		}
	}
	commit(types.SET_PLAYLIST, playList)
	commit(types.SET_SEQUENCELIST, sequenceList)
	commit(types.SET_CURRENT_INDEX, currentIndex)
	commit(types.SET_FULLSCREEN, true)
	commit(types.SET_PLAYING, true)
}

export const saveSearch = function({
	commit
}, query) {
	commit(types.SET_SEARCH_HISTORY, searchHistory(query))
}

export const deleteSearchHistory = function({
	commit
}, query) {
	commit(types.SET_SEARCH_HISTORY, deleteSearch(query))
}

export const clearSearchHistory = function({
	commit
}) {
	commit(types.SET_SEARCH_HISTORY, clearSearch)
}

export const deleSong = function({
	commit,state
}, song) {
	let playList = state.playList.slice()
	let sequenceList = state.sequenceList.slice()
	let currentIndex = state.currentIndex
	let pIndex = findIndex(playList, song)
	playList.splice(pIndex, 1)
	let sIndex = findIndex(sequenceList, song)
	sequenceList.splice(sIndex, 1)
	if(currentIndex > pIndex || currentIndex === playList.length) {
		currentIndex--
	}
    
	commit(types.SET_PLAYLIST, playList)
	commit(types.SET_SEQUENCELIST, sequenceList)
	commit(types.SET_CURRENT_INDEX, currentIndex)
	if(!playList.length) {
		commit(types.SET_PLAYING, false)
	} else {
		commit(types.SET_PLAYING, true)
	}
}


export const deleteSongList=function({commit}){
	commit(types.SET_PLAYLIST, [])
	commit(types.SET_SEQUENCELIST, [])
	commit(types.SET_CURRENT_INDEX, -1)
	commit(types.SET_PLAYING, false)
}



export const  saveHistory=function({commit},song){
	commit(types.SET_PLAY_HISTORY, playHistory(song))
}



