<template>
	<div class="music-list">
		<div class="back" @click="back">
			<i class="icon-back"></i>
		</div>
		<h1 class="title" v-html="title"></h1>
		<div class="bg-image" :style="bgStyle" ref="bgImage">
			<div class="filter" ref="filter"></div>
			<div class="play-wrapper" v-if="songs.length>0&&scrollY>=0">
				<div class="play">
					<i class="icon-play"></i>
					<span class="text" @click="random(songs)">随机播放全部</span>
				</div>
			</div>

		</div>
		<div class="bg-layer" ref="bgLayer"></div>
		<scroll :data="songs" class="list" ref="list" @scroll="scroll" :listenScroll="listenScroll" :probeType="probeType">
			<div class="song-list-wrapper">
				<song-list :rank="rank" :songs="songs" @select="selectItem"></song-list>
			</div>
			<loading v-if="!songs.length" class="loading-container"></loading>
		</scroll>
	</div>
</template>

<script>
	import Scroll from '@/base/scroll/scroll'
	import Loading from '@/base/loading/loading'
	import songList from '@/base/song-list/song-list'
	import { prefixStyle } from '@/common/js/dom'
    import {mapActions} from "vuex"
    import {playListMixin} from '@/common/js/mixin'
    
	const RESERVED_HEIGHT = 40
	const transform = prefixStyle("transform")
	const backdrop = prefixStyle("backdrop-filter")
	export default {
		mixins:[playListMixin],
		props: {
			songs: {
				type: Array,
				default:[]
			},
			title: {
				type: String,
				default: ""
			},
			bgImg: {
				type: String,
				default: ""
			},
			rank:{
				type:Boolean,
				default:false
			}
		},
		data() {
			return {
				scrollY: 0
			}
		},
		created() {
			this.probeType = 3
			this.listenScroll = true
		},
		mounted() {
			this.imageHeight = this.$refs.bgImage.clientHeight
			this.minTransalteY = -this.imageHeight + RESERVED_HEIGHT
			this.$refs.list.$el.style.top = `${this.imageHeight}px`
		},
		computed: {
			bgStyle() {
				return `background-image:url(${this.bgImg});`
			},

		},
		methods: {
			back() {
				this.$router.back()
			},
			scroll(pos) {
				this.scrollY = pos.y
			},
			selectItem(item,index){
				this.selectPlay({
					list:this.songs,
					index:index
				})
			},
			random(item){
				this.randomPlay({
					list:this.songs
				})
			},
			handleMixin(playList){
				const bottom=playList.length>0? '60px':''
				this.$refs.list.$el.style.bottom=bottom
				this.$refs.list.refresh()
			},
			...mapActions([
				"selectPlay",
				"randomPlay"
			])
		},
		components: {
			Scroll,
			songList,
			Loading
		},
		watch: {
			scrollY(newVal) {
				let translateY = Math.max(this.minTransalteY, newVal)
				this.$refs.bgLayer.style[transform] = `translate3d(0,${translateY}px,0)`
				let zIndex = 0;
				let scale = 1;
				let blur = 0;
				let precent = Math.abs(newVal / this.imageHeight)
				//处理背景图片的zindex和高度
				if(newVal < this.minTransalteY) {
					zIndex = 10
					this.$refs.bgImage.style.paddingTop = 0
					this.$refs.bgImage.style.height = `${RESERVED_HEIGHT}px`
				} else {
					zIndex = 0
					this.$refs.bgImage.style.paddingTop = "70%"
					this.$refs.bgImage.style.height = 0
				}

				//图片缩放和ios高斯模糊
				if(newVal > 0) {
					zIndex = 10
					scale = 1 + precent
					this.$refs.bgImage.style[transform] = `scale(${scale})`
				} else {
					blur = Math.min(20 * precent, 20)
					this.$refs.bgImage.style[backdrop] = `blur(${blur}px)`
				}
				this.$refs.bgImage.style.zIndex = zIndex
			}
		}
	}
</script>

<style lang="scss" scoped="">
	@import '../../common/sass/variable';
	@import '../../common/sass/icon.css';
	@import '../../common/sass/mixin';
	.music-list {
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 100;
		width: 100%;
		background: $color-background;
		.back {
			position: absolute;
			top: 0;
			left: 6px;
			z-index: 50;
			.icon-back {
				display: block;
				padding: 10px;
				font-size: $font-size-large-x;
				color: $color-theme;
			}
		}
		.title {
			position: absolute;
			top: 0;
			left: 10%;
			z-index: 40;
			width: 80%;
			@include no-wrap();
			text-align: center;
			line-height: 40px;
			font-size: $font-size-large;
			color: $color-text;
		}
		.bg-image {
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 70%;
			transform-origin: top;
			background-size: cover;
			.filter {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: rgba(7, 17, 27, 0.4);
			}
			.play-wrapper {
				position: absolute;
				bottom: 20px;
				z-index: 50;
				width: 100%;
				.play {
					box-sizing: border-box;
					width: 135px;
					padding: 7px 0;
					margin: 0 auto;
					text-align: center;
					border: 1px solid $color-theme;
					color: $color-theme;
					border-radius: 100px;
					font-size: 0;
					.icon-play {
						display: inline-block;
						vertical-align: middle;
						margin-right: 6px;
						font-size: $font-size-medium-x;
					}
					.text {
						display: inline-block;
						vertical-align: middle;
						font-size: $font-size-small;
					}
				}
			}
		}
		.bg-layer {
			position: relative;
			height: 100%;
			background: $color-background;
		}
		.list {
			position: fixed;
			top: 0;
			bottom: 0;
			width: 100%;
			background: $color-background;
			.song-list-wrapper {
				padding: 20px 30px;
			}
			.loading-container {
				position: absolute;
				width: 100%;
				top: 50%;
				transform: translateY(-50%);
			}
		}
	}
</style>


