<template>
	<div class="progress-circle">
		<svg class="circle" :width="radius" :height="radius" viewBox="0 0 100 100" version="1.1" xmlns="http://www.w3.org/2000/svg">
			<circle class="progress-background" r="50" cx="50" cy="50" fill="transparent" />
			<circle class="progress-bar" r="50" cx="50" cy="50" fill="transparent" :stroke-dasharray="strokeDasharray" :stroke-dashoffset="strokeDashoffset"/>
		</svg>
		<slot></slot>
	</div>
</template>

<script>
	export default {
		props:{
			radius:{
				type:Number,
				default:10
			},
			percent:{
				type:Number,
				default:0
			}
		},
		data(){
			return {
				strokeDasharray:314
				
			}
		},
		computed:{
			strokeDashoffset(){
				return this.strokeDasharray*(1-this.percent)
			}
		}
	}
</script>

<style lang="scss" scoped="">
	@import "../../common/sass/variable";
	.progress-circle {
		position: relative;
		circle {
			stroke-width: 8px;
			transform-origin:center;
			&.progress-background {
				transform: scale(0.9);
				stroke: $color-theme-d;
			}
			&.progress-bar {
				transform: scale(0.9) rotate(-90deg);
				stroke: $color-theme;
			}
		}
	}
</style>


