<template>
	<div class="player" v-show="playList.length>0">
		<transition name="normal" @enter="enter" @after-enter="afterEnter" @leave="leave" @after-leave="afterLeave">

			<div class="normal-player" v-show="fullScreen">
				<div class="background">
					<img :src="currentSong.image" alt="" width="100%" height="100%" />
				</div>
				<div class="top">
					<div class="back" @click="back">
						<i class="icon-back"></i>
					</div>
					<h1 class="title" v-html="currentSong.name"></h1>
					<h2 class="subtitle" v-html="currentSong.singer"></h2>
				</div>
				<div class="middle" @touchstart.prevent="middleTouchStart" @touchmove.prevent="middleTouchMove" @touchend.prevent="middleTouchEnd">
					<div class="middle-l" ref="middleCd">
						<div class="cd-wrapper" ref="cdWrapper">
							<div class="cd" :class="cdcls" ref="cd">
								<img :src="currentSong.image" alt="" class="image" />
							</div>
						</div>
						<div class="playing-lyric-wrapper">
							<div class="playing-lyric">{{playingLyric}}</div>
						</div>
					</div>
					<scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines">
						<div class="lyric-wrapper">
							<div v-if="currentLyric">
								<p ref="lyricLine" class="text" :class="{'current':currentLineNum===index}" v-for="(line,index) in currentLyric.lines">{{line.txt}}</p>
							</div>
						</div>
					</scroll>
				</div>
				<div class="bottom">
					<div class="dot-wrapper">
						<span class="dot" :class="{'active':currentShow==='cd'}"></span>
						<span class="dot" :class="{'active':currentShow==='lyric'}"></span>
					</div>
					<div class="progress-wrapper">
						<span class="time time-l">{{formatTime(currentTime)}}</span>
						<div class="progress-bar-wrapper">
							<progress-bar :percent="percent" @percentChange="onChangePercent"></progress-bar>
						</div>
						<span class="time time-r">{{formatTime(currentSong.duration)}}</span>
					</div>
					<div class="operators">
						<div class="icon i-left">
							<i :class="getMode" @click="changeMode"></i>
						</div>
						<div class="icon i-left" @click="prev" :class="disabled">
							<i class="icon-prev"></i>
						</div>
						<div class="icon i-center" @click="togglePlaying" :class="disabled">
							<i :class="playingIcon"></i>
						</div>
						<div class="icon i-right" @click="next" :class="disabled">
							<i class="icon-next"></i>
						</div>
						<div class="icon i-right">
							<i class="icon icon-not-favorite"></i>
						</div>

					</div>
				</div>
			</div>
		</transition>
		<transition name="mini">
			<div class="mini-player" v-show="!fullScreen" @click="open">
				<div class="icon">
					<img :class="cdcls" ref="miniCd" :src="currentSong.image" alt="" width="40" height="40" />
				</div>
				<div class="text">
					<h2 class="name" v-html="currentSong.name"></h2>
					<p class="desc" v-html="currentSong.singer"></p>
				</div>
				<div class="control" @click.stop="togglePlaying">
					<progress-circle :percent="percent" :radius="radius">
						<i :class="miniIcon" class="icon-mini"></i>
					</progress-circle>

				</div>
				<div class="control">
					<i class="icon-playlist" @click.stop="showPlaylist" ></i>
				</div>
			</div>

		</transition>
		<playlist ref="playlist"></playlist>
		<audio :src="currentSong.url" ref="audio" @play="ready" @error="error" @timeupdate="updateTime" @ended="end"></audio>

	</div>
</template>

<script>
	import {ERR_OK} from "@/api/config"
	import { mapGetters, mapMutations,mapActions } from 'vuex'
	import animations from 'create-keyframe-animation'
	import { prefixStyle } from '@/common/js/dom'
	import ProgressBar from '@/base/progress-bar/progress-bar'
	import ProgressCircle from '@/base/progress-circle/progress-circle'
	import { playMode } from '@/common/js/config'
	import Lyric from 'lyric-parser'
	import Scroll from '@/base/scroll/scroll'
	import Playlist from '@/components/playlist/playlist'
    import {playerMixin} from '@/common/js/mixin'
	
	const transform = prefixStyle("transform")
	const transition = prefixStyle('transition')
	export default {
		mixins:[playerMixin],
		data() {
			return {
				isReady: false,
				currentTime: 0,
				radius: 32,
				currentLyric: null,
				currentLineNum: 0,
				currentShow: "cd",
				playingLyric: ""
			}
		},
		created() {
			this.touch = {}

		},
		computed: {
			playingIcon() {
				return this.playing ? "icon-pause" : "icon-play"
			},
			miniIcon() {
				return this.playing ? "icon-pause-mini" : "icon-play-mini"
			},
			cdcls() {
				return this.playing ? "play" : "pause"
			},
			disabled() {
				return this.isReady ? "" : "disable"
			},
			percent() {
				return this.currentTime / this.currentSong.duration
			}
		},
		methods: {
			back() {
				this.setFullScreen(false)
			},
			open() {
				this.setFullScreen(true)
			},
			showPlaylist(){
				this.$refs.playlist.show()
			},
			enter(el, done) {
				const {
					x,
					y,
					scale
				} = this._getInitStyle()
				let animation = {
					0: {
						transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
					},
					60: {
						transform: `translate3d(0,0,0) scale(1.1)`
					},
					100: {
						transform: `translate3d(0,0,0) scale(1)`
					}
				}
				animations.registerAnimation({
					name: "move",
					animation,
					presets: {
						duration: 400,
						easing: "linear"
					}
				})
				animations.runAnimation(this.$refs.cdWrapper, "move", done)
			},
			afterEnter() {
				animations.unregisterAnimation('move')
				this.$refs.cdWrapper.style.animation = ''
			},
			leave(el, done) {
				this.$refs.cdWrapper.style.transition = 'all 0.4s'
				const {
					x,
					y,
					scale
				} = this._getInitStyle()
				this.$refs.cdWrapper.style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`
				this.$refs.cdWrapper.addEventListener('transitionend', done)
			},
			afterLeave() {
				this.$refs.cdWrapper.style.transition = ''
				this.$refs.cdWrapper.style[transform] = ''
			},
			togglePlaying() {
				if(!this.isReady) {
					return
				}
				this.setPlaying(!this.playing)
				this.currentLyric.togglePlay()
			},
			prev() {
				if(!this.isReady) {
					return
				}
				if(this.playList.length == 1) {
					this.loop()
				} else {
					let index = this.currentIndex - 1
					if(index < 0) {
						index = this.playList.length - 1
					}
					this.setCurrentIndex(index)
					if(!this.playing) {
						this.togglePlaying()
					}
				}

				this.isReady = false
			},
			next() {
				if(!this.isReady) {
					return
				}
				if(this.playList.length == 1) {
					this.loop()
				} else {
					let index = this.currentIndex + 1
					if(index === this.playList.length) {
						index = 0
					}
					this.setCurrentIndex(index)
					if(!this.playing) {
						this.togglePlaying()
					}
				}

				this.isReady = false
			},
			ready() {
				this.isReady = true
			    this.saveHistory(this.currentSong)
			},
			error() {
				this.isReady = true
			},
			updateTime(e) {
				this.currentTime = e.target.currentTime

			},
			formatTime(time) {
				time = time | 0
				let minutes = time / 60 | 0
				let seconds = time % 60
				if(seconds <= 9) {
					seconds = '0' + seconds
				}
				return `${minutes}:${seconds}`
			},
			onChangePercent(percent) {
				this.$refs.audio.currentTime = this.currentSong.duration * percent
				if(!this.playing) {
					this.togglePlaying()
				}
				this.currentLyric.seek(this.currentSong.duration * percent * 1000)
			},
			end() {
				if(this.mode === playMode.loop) {
					this.loop()
				} else {
					this.next()
				}
			},
			loop() {
				this.$refs.audio.currentTime = 0
				this.$refs.audio.play()
				this.currentLyric.seek(0)
			},
			getLyric() {
				this.currentSong.getSongWord().then((lyric) => {
					lyric = lyric.replace(/&#(\d+);/g, (str, match) => String.fromCharCode(match))
					this.currentLyric = new Lyric(lyric, this.handleLyric)
					if(this.playing) {
						this.currentLyric.play()
					}
				}).catch(() => {
					this.currentLyric = null
					this.playingLyric = ''
					this.currentLineNum = 0
				})
			},
			handleLyric({
				lineNum,
				txt
			}) {
				this.currentLineNum = lineNum
				if(lineNum > 5) {
					let lineEl = this.$refs.lyricLine[lineNum - 5]
					this.$refs.lyricList.scrollToElement(lineEl, 1000)
				} else {
					this.$refs.lyricList.scrollTo(0, 0, 1000)
				}
				this.playingLyric = txt
			},
			middleTouchStart(e) {
				this.touch.initiated = true
				// 用来判断是否是一次移动
				this.touch.moved = false
				const touch = e.touches[0]
				this.touch.startX = touch.pageX
				this.touch.startY = touch.pageY
			},
			middleTouchMove(e) {
				const touch = e.touches[0]
				const delX = touch.pageX - this.touch.startX
				const delY = touch.pageY - this.touch.startY
				if(Math.abs(delY) > Math.abs(delX)) {
					return
				}
				if(!this.touch.moved) {
					this.touch.moved = true
				}
				const left = this.currentShow === 'cd' ? 0 : -window.innerWidth
				const offsetWidth = Math.min(0, Math.max(-window.innerWidth, left + delX))
				this.touch.percent = Math.abs(offsetWidth / window.innerWidth)
				this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
				this.$refs.middleCd.style.opacity=1 - this.touch.percent
				this.$refs.middleCd.style[transition] = 'all 0.3s'
				this.$refs.lyricList.$el.style[transition] = 'all 0.3s'
			},
			middleTouchEnd(e) {
				if(!this.touch.moved) {
					return
				}
				let offsetWidth,opacity
				if(this.currentShow === "cd") {
					if(this.touch.percent > 0.1) {
						offsetWidth = -window.innerWidth
						opacity=0
						this.currentShow = "lyric"
					} else {
						offsetWidth = 0
						opacity=1
					}
				} else {

					if(this.touch.percent < 0.9) {
						offsetWidth = 0
						opacity=1
						this.currentShow = "cd"
					} else {
						opacity=0
						offsetWidth = -window.innerWidth
					}
				}
				this.$refs.lyricList.$el.style[transition] = 'all 0.3s'
				this.$refs.middleCd.style[transition] = 'all 0.3s'
				this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
				this.$refs.middleCd.style.opacity=opacity
				this.touch.initiated = false
			},
			_getInitStyle() {
				const targetWidth = 40
				const paddingTop = 80
				const paddingLeft = 40
				const paddingBottom = 30
				const width = window.innerWidth * 0.8
				const scale = targetWidth / width
				const x = -(window.innerWidth / 2 - paddingLeft)
				const y = window.innerHeight - paddingTop - paddingBottom - width / 2
				return {
					x,
					y,
					scale
				}
			},
			...mapActions(["saveHistory"])
		},
		watch: {
			currentSong(newSong, oldSong) {
				if(!newSong.id||newSong.id === oldSong.id) {
					return
				}
				if(this.currentLyric) {
					this.currentLyric.stop()
				}
				clearTimeout(this.timer)
				this.timer = setTimeout(() => {
					this.$refs.cd.style[transform] = ""
					this.$refs.miniCd.style[transform] = ""
					this.$refs.audio.play()
					this.getLyric()
				}, 200)
			},
			playing(newPlaying) {
				const audio = this.$refs.audio
				this.$nextTick(() => {
					newPlaying ? audio.play() : audio.pause()
				})

			}
		},
		components: {
			ProgressBar,
			ProgressCircle,
			Scroll,
			Playlist
		}
	}
</script>

<style lang="scss" scoped="">
	@import '../../common/sass/variable';
	@import '../../common/sass/mixin';
	@import '../../common/sass/icon.css';
	.player {
		.normal-player {
			position: fixed;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			width: 100%;
			z-index:180;
			background: $color-background;
			.background {
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				z-index: -1;
				opacity: 0.6;
				filter: blur(20px);
			}
			.top {
				position: relative;
				margin-bottom: 25px;
				.back {
					position: absolute;
					top: 0;
					left: 6px;
					z-index: 50;
					.icon-back {
						display: block;
						padding: 9px;
						font-size: $font-size-large-x;
						color: $color-theme;
						transform: rotate(-90deg);
					}
				}
				.title {
					width: 70%;
					margin: 0 auto;
					line-height: 40px;
					text-align: center;
					@include no-wrap();
					font-size: $font-size-large;
					color: $color-text;
				}
				.subtitle {
					line-height: 20px;
					text-align: center;
					font-size: $font-size-medium;
					color: $color-text;
				}
			}
			.middle {
				position: fixed;
				width: 100%;
				top: 80px;
				bottom: 150px;
				white-space: nowrap;
				font-size: 0;
				.middle-l {
					display: inline-block;
					vertical-align: top;
					position: relative;
					width: 100%;
					height: 0;
					padding-top: 60%;
					.cd-wrapper {
						position: absolute;
						left: 20%;
						top: 0;
						width: 60%;
						height: 100%;
						.cd {
							width: 100%;
							height: 100%;
							box-sizing: border-box;
							border: 10px solid rgba(255, 255, 255, 0.1);
							border-radius: 50%;
							overflow: hidden;
							&.play {
								animation: rotate 20s linear infinite;
							}
							&.pause {
								animation-play-state: paused;
							}
							.image {
								width: 100%;
								height: 100%;
								border-radius: 50%;
							}
						}
					}
					.playing-lyric-wrapper {
						width: 80%;
						margin: 30px auto 0 auto;
						overflow: hidden;
						text-align: center;
						.playing-lyric {
							height: 20px;
							line-height: 20px;
							font-size: $font-size-medium;
							color: $color-text-l;
						}
					}
				}
				.middle-r {
					display: inline-block;
					vertical-align: top;
					width: 100%;
					height: 100%;
					overflow: hidden;
					.lyric-wrapper {
						width: 80%;
						margin: 0 auto;
						overflow: hidden;
						text-align: center;
						.text {
							line-height: 32px;
							color: $color-text-l;
							font-size: $font-size-medium;
							&.current {
								color: $color-text;
							}
						}
					}
				}
			}
			.bottom {
				position: absolute;
				bottom:20px;
				width: 100%;
				.dot-wrapper {
					text-align: center;
					font-size: 0;
					.dot {
						display: inline-block;
						vertical-align: middle;
						margin: 0 4px;
						width: 8px;
						height: 8px;
						border-radius: 50%;
						background: $color-text-l;
						&.active {
							width: 20px;
							border-radius: 5px;
							background: $color-text-ll;
						}
					}
				}
				.progress-wrapper {
					display: flex;
					align-items: center;
					width: 80%;
					margin: 0px auto;
					padding: 10px 0;
					.time {
						color: $color-text;
						font-size: $font-size-small;
						flex: 0 0 30px;
						line-height: 30px;
						width: 30px;
						&.time-l {
							text-align: left;
						}
						&.time-r {
							text-align: right;
						}
					}
					.progress-bar-wrapper {
						flex: 1;
					}
				}
				.operators {
					display: flex;
					align-items: center;
					.icon {
						flex: 1;
						color: $color-theme;
						&.disable {
							color: $color-theme-d;
						}
						i {
							font-size: 30px;
						}
					}
					.i-left {
						text-align: right
					}
					.i-center {
						padding: 0 20px;
						text-align: center;
						i {
							font-size: 40px;
						}
					}
					.i-right {
						text-align: left;
					}
					.icon-favorite {
						color: $color-sub-theme;
					}
				}
			}
			&.normal-enter-active,
			&.normal-leave-active {
				transition: all 0.4s;
				.top,
				.bottom {
					transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);
				}
			}
			&.normal-enter,
			&.normal-leave-to {
				opacity: 0;
				.top {
					transform: translate3d(0, -100px, 0);
				}
				.bottom {
					transform: translate3d(0, 100px, 0);
				}
			}
		}
		.mini-player {
			display: flex;
			align-items: center;
			position: fixed;
			left: 0;
			bottom: 0;
			z-index: 180;
			width: 100%;
			height: 60px;
			background: $color-highlight-background;
			&.mini-enter-active,
			&.mini-leave-active {
				transition: all 0.4s;
			}
			&.mini-enter,
			&.mini-leave-to {
				opacity: 0;
			}
			.icon {
				flex: 0 0 40px;
				width: 40px;
				padding: 0 10px 0 20px;
				img {
					border-radius: 50%;
					&.play {
						animation: rotate 10s linear infinite;
					}
					&.pause {
						animation-play-state: paused;
					}
				}
			}
			.text {
				display: flex;
				flex-direction: column;
				justify-content: center;
				flex: 1;
				line-height: 20px;
				overflow: hidden;
				.name {
					margin-bottom: 2px;
					@include no-wrap();
					font-size: $font-size-medium;
					color: $color-text;
				}
				.desc {
					@include no-wrap();
					font-size: $font-size-small;
					color: $color-text-d;
				}
			}
			.control {
				flex: 0 0 30px;
				width: 30px;
				padding: 0 10px;
				.icon-play-mini,
				.icon-pause-mini,
				.icon-playlist {
					font-size: 30px;
					color: $color-theme-d;
				}
				.icon-mini {
					font-size: 32px;
					position: absolute;
					left: 0;
					top: 0;
				}
			}
		}
	}
	
	@keyframes rotate {
		0% {
			transform: rotate(0);
		}
		100% {
			transform: rotate(360deg);
		}
	}
</style>


